<template>
  <v-container>
    <v-card-text>
      <v-form v-model="valid" ref="form" lazy-validation>
        <v-row>
          <v-col cols="12" sm="6">
            <v-select
              label="Subject"
              v-model="question.subject"
              :items="subjects"
              @change="getChapters"
              :rules="requiredRules"
              outlined
              dense
            />
          </v-col>
          <v-col cols="12" sm="6" v-if="question.subject != 'অন্যান্য'">
            <v-select
              label="Chapters"
              v-model="question.chapter"
              :items="[...chapters, 'Others']"
              :rules="requiredRules"
              outlined
              dense
            />
          </v-col>
        </v-row>

        <v-file-input
          multiple
          outlined
          dense
          label="Picture"
          @change="uploadImage"
          :loading="loading"
        />

        <v-row v-if="question.images.length > 0">
          <v-col
            cols="6"
            md="3"
            class="imageContainer"
            v-for="(img, i) in question.images"
            :key="i"
          >
            <v-avatar tile size="180" class="mb-5">
              <img :src="img" />
              <v-btn
                fab
                tile
                dark
                small
                class="error delete__button"
                text
                @click="deleteImage(img)"
              >
                <v-icon>mdi-delete</v-icon></v-btn
              >
            </v-avatar>
          </v-col>
        </v-row>

        <v-textarea
          :rules="requiredRules"
          v-model="question.text"
          outlined
          label="Your question"
        />
      </v-form>
      <div class="text-center">
        <v-btn
          @click="submitQuestion"
          :loading="loading"
          class="success"
          large
          rounded
          >Submit Your Question</v-btn
        >
      </div>
    </v-card-text>
  </v-container>
</template>

<script>
import { subjects } from "../plugins/subject";
export default {
  data() {
    return {
      question: {
        subject: "",
        chapter: "",
        text: "",
        images: [],
        time: new Date().toLocaleString(),
      },
      allsubjects: [],
      subjects: [],
      chapters: [],
      subject: "",
      chapter: "",
      questionData: {},
      loading: false,
      valid: true,
      requiredRules: [(v) => !!v || "This field is required"],
    };
  },
  methods: {
    uploadImage(files) {
      files.forEach((file) => {
        var reader = new FileReader();
        reader.onloadend = () => {
          this.question.images.push(reader.result);
        };
        reader.readAsDataURL(file);
      });
    },
    deleteImage(img) {
      this.question.images.splice(img, 1);
    },
    submitQuestion() {
      if (this.$refs.form.validate()) {
        this.loading = !this.loading;

        let { subject, chapter, text, images, time } = this.question;

        this.questionData = {
          subject,
          images,
          question: [time, subject, chapter, text],
        };
        // console.log(JSON.stringify(this.questionData));
        let url =
          "https://script.google.com/macros/s/AKfycbySJmTvP5rMSbFa0NknlJ9NgXKn4ehNsU3ddISUJnMFoYsxKiIHbr-Ty74ec0s3mqVS/exec";
        fetch(url, {
          method: "POST",
          mode: "no-cors",
          cache: "no-cache",
          redirect: "follow",
          body: JSON.stringify(this.questionData),
        })
          .then(() => {
            this.loading = !this.loading;
            this.$swal({
              icon: "success",
              title: "Successfully submitted",
            }).then(() => {
              this.$emit("closeDialog");
              this.question = {
                subject: "",
                chapter: "",
                text: "",
                images: [],
                time: new Date().toLocaleString(),
              };
              this.subject = "";
              this.chapter = "";
              this.valid = true;
            });
          })
          .catch((e) => console.log(e));
      }
    },
    getSubjects() {
      this.question.chapter = "";

      this.allsubjects = subjects;
      this.subjects = this._.union(this.allsubjects.map((s) => s.subject));
    },
    getChapters() {
      this.chapters = [];
      this.allsubjects.forEach((s) => {
        if (s.subject === this.question.subject) {
          this.chapters.push(s.chapter);
        }
      });
    },
  },
  created() {
    this.getSubjects();
  },
};
</script>

<style lang="scss" scoped>
.imageContainer {
  position: relative;
}

.delete__button {
  position: absolute;
  top: 0;
  left: 0;
}
</style>