import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/login",
    name: "Auth",
    component: () => import("../views/Auth.vue"),
  },
  {
    path: "/exam",
    name: "Exam",
    component: () => import("../views/exams/Exams.vue"),
  },
  {
    path: "/exam/:id",
    name: "ExamQuestion",
    meta: { requiresAuth: true },
    component: () => import("../views/exams/MCQuestion.vue"),
  },
  {
    path: "/ranking/:id",
    name: "Ranking",
    meta: { requiresAuth: true },
    component: () => import("../views/exams/MCQLeaderboard.vue"),
  },
  {
    path: "/about",
    name: "About",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/About.vue"),
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});
router.beforeEach((to, from, next) => {
  const requiresAuth = to.matched.some((record) => record.meta.requiresAuth);

  let authenticated = !localStorage.getItem("user");

  if (authenticated && requiresAuth) {
    next({
      path: "/login",
      query: { redirect: to.fullPath },
    });
  } else {
    next();
  }
});
export default router;
