<template>
  <div>
    <div v-for="c in currentClass" :key="c.id">
      <v-card dark class="my-4 mb-5 current__event__container">
        <v-card-title>
          <h2
            class="ma-auto hind_shiliguri challenge__title success--text text--lighten-1"
          >
            {{ c.live_class ? "On going..." : "Upcoming Class" }}
          </h2>
        </v-card-title>
        <v-card-text class="white--text hind_shiliguri">
          <v-row justify="center" align="center">
            <v-col cols="12" class="text-center">
              <h1 class="mb-4 text-center">{{ c.subject }}</h1>

              <p class="desc chapter balooda text-center">
                {{ c.chapter }}
              </p>
              <p class="desc chapter balooda text-center">
                {{ c.instructor }}
              </p>
              <p class="desc date">
                {{ c.date | moment("MMMM Do YYYY, h:mm:ss a") }}
              </p>
            </v-col>
            <v-col cols="12">
              <div style="max-width: 500px" class="ma-auto">
                <Timer :end="c.date" />
              </div>
            </v-col>
          </v-row>

          <div class="currentTaskButtons mt-3">
            <v-btn
              large
              dark
              color="primary"
              v-if="c.date + 60000 * 90 > Date.now() && c.live_class"
              :href="c.live_class"
              target="_blank"
            >
              <v-icon class="mr-1">mdi-antenna</v-icon>
              Live Class
            </v-btn>
            <v-btn
              @click="showPopup"
              v-else-if="!c.live_class && c.date < Date.now()"
              large
              dark
              color="primary"
              target="_blank"
            >
              <v-icon class="mr-1">mdi-antenna</v-icon>
              Live Class
            </v-btn>
            <v-btn
              @click="showPopup"
              v-else
              disabled
              large
              dark
              color="primary"
            >
              <v-icon class="mr-1">mdi-antenna</v-icon>
              Live Class
            </v-btn>
            <v-btn
              large
              dark
              :href="c.lecture_sheet"
              target="_blank"
              color="primary"
              v-if="c.lecture_sheet"
              >Lecture sheet</v-btn
            >
            <v-btn large dark color="primary" disabled v-else
              >Lecture sheet</v-btn
            >
            <!-- <v-btn
              large
              dark
              color="primary"
              :href="c.hand_note"
              :disabled="!c.hand_note"
              target="_blank"
              >hand note & HW</v-btn
            > -->
          </div>
        </v-card-text>
      </v-card>
    </div>
  </div>
</template>

<script>
import Timer from "./Timer";
// import axios from "axios";
export default {
  props: {
    upclasses: {
      required: true,
    },
  },
  components: { Timer },
  data() {
    return {
      currentClass: [],
    };
  },

  methods: {
    showPopup() {
      this.$swal({
        icon: "warning",
        title: "Upcoming..",
        text: "Please wait a while... or reload the page",
      });
    },
    lookupChanges() {
      setInterval(() => {
        this.currentClass = [];
        let currentClass = [];

        this.upclasses.forEach((c) => {
          if (c.date > Date.now() - 60000 * 90) {
            currentClass.push(c);
          }
        });
        // currentClass.sort((a, b) => a.date - b.date);
        this.currentClass.push(currentClass[0]);
        if (currentClass.length >= 2) {
          if (currentClass[0].date === currentClass[1].date) {
            this.currentClass.push(currentClass[1]);
          }
        }
      }, 3000);
    },
  },
  // watch: {
  //   currentClass: () => {
  //     let currentClass = [];
  //     this.upclasses.forEach((c) => {
  //       if (new Date(c.date).getTime() > Date.now() - 60000 * 90) {
  //         currentClass.push(c);
  //       }
  //     });
  //     return currentClass[0];
  //   },
  // },
  created() {
    this.lookupChanges();
  },
};
</script>

<style lang="scss" scoped>
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap");
.tasks {
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
  justify-content: space-between;
  align-items: center;
}
.textdoc {
  position: relative;
}
.submit_challenge {
  position: absolute;
  top: 0;
  right: 0;
}
.currentTaskButtons {
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
  justify-content: center;
  align-items: center;
}
.currentTaskButtons > .v-btn {
  box-shadow: 33px 33px 66px #114c68b9 !important;
  font-family: "Poppins", sans-serif !important;
  font-weight: bold;
}
.current__event__container {
  overflow: hidden;
  position: relative;
  border-radius: 35px !important;

  /* background: linear-gradient(145deg, #0e0420, #2b2736);
  box-shadow: 33px 33px 66px #221f2b, -33px -33px 66px #2e293a; */
  background: rgba(0, 0, 0, 0.4) !important;
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37) !important;
  backdrop-filter: blur(10px) !important;
  -webkit-backdrop-filter: blur(4px) !important;
  border: 1px solid rgba(255, 255, 255, 0.18) !important;
}
.challenge__title {
  border-bottom: 2px solid #fff;
  padding-bottom: 5px;
}
.desc {
  font-weight: 500;
}
.chapter {
  font-size: 18px;
}
.date {
  font-size: 15px;
  font-family: "Poppins", sans-serif !important;
}
.timer {
  font-size: 17px;
  font-family: "Poppins", sans-serif !important;
}
</style>