<template>
  <v-container>
    <CurrentEvent :upclasses="classes" v-if="classes.length > 0" />
    <v-card class="backdark" dark>
      <v-card-text>
        <div class="subject__filters mb-3">
          <button
            :class="`subject__button ${se == '' && 'activebtn'}`"
            @click="selectSubject('')"
          >
            All
          </button>
          <button
            v-for="(s, i) in subjects"
            :key="i"
            :class="`subject__button ${se == s && 'activebtn'}`"
            @click="selectSubject(s)"
          >
            {{ s }}
          </button>
        </div>
      </v-card-text>

      <v-card-title class="my-n7">
        <!-- <v-btn @click="previous = !previous" color="pink  my-2" dark large>
          {{ previous ? "Hide previous classes" : "show All classes" }}</v-btn
        > -->

        <v-sheet max-width="300px" class="ma-auto transparent">
          <v-text-field
            v-model="search"
            outlined
            dense
            rounded
            label="Search"
            append-icon="mdi-magnify"
          />
        </v-sheet>
      </v-card-title>
      <v-card-text>
        <v-data-table
          class="transparent"
          :loading="selected.length < 1"
          :headers="headers"
          :search="search"
          :items="selected"
        >
          >
          <template v-slot:item.live_class="{ item }">
            <v-btn
              fab
              dark
              small
              color="primary"
              target="_blank"
              :disabled="!item.live_class"
              :href="item.live_class"
            >
              <svg style="width: 24px; height: 24px" viewBox="0 0 24 24">
                <path
                  fill="currentColor"
                  d="M20,17A2,2 0 0,0 22,15V4A2,2 0 0,0 20,2H9.46C9.81,2.61 10,3.3 10,4H20V15H11V17M15,7V9H9V22H7V16H5V22H3V14H1.5V9A2,2 0 0,1 3.5,7H15M8,4A2,2 0 0,1 6,6A2,2 0 0,1 4,4A2,2 0 0,1 6,2A2,2 0 0,1 8,4Z"
                />
              </svg>
            </v-btn>
          </template>
          <template v-slot:item.lecture_sheet="{ item }">
            <v-btn
              dark
              small
              fab
              color="success"
              target="_blank"
              :href="item.lecture_sheet"
              :disabled="!item.lecture_sheet"
            >
              <v-icon>mdi-fountain-pen-tip</v-icon>
            </v-btn>
          </template>

          <template v-slot:item.date="{ item }">
            {{ item.date | moment("MMMM Do YYYY, h:mm:ss a") }}
          </template>
        </v-data-table>
      </v-card-text>
    </v-card>
  </v-container>
</template>

<script>
import axios from "axios";

import CurrentEvent from "@/components/CurrentEvent";
export default {
  data() {
    return {
      selectedChallenge: {},
      classes: [],
      selected: [],
      upcoming: [],
      subjects: new Set(),
      dialog: false,
      search: "",
      se: "",
      previous: false,
      headers: [
        { text: "Subject", sortable: false, value: "subject" },
        {
          text: "Chapter/Topic",
          align: "center",
          sortable: false,
          value: "chapter",
        },
        { text: "Instructor", sortable: false, value: "instructor" },

        { text: "Date & Time", value: "date" },
        {
          text: "Live Class",
          value: "live_class",
          sortable: false,
          align: "center",
        },
        {
          text: "Lecture Sheet",
          value: "lecture_sheet",
          sortable: false,
          align: "center",
        },
      ],
    };
  },
  components: { CurrentEvent },
  methods: {
    viewChallenge(item) {
      this.dialog = true;
      this.selectedChallenge = item;
    },

    getRoutineInfo() {
      axios
        .get(
          "https://script.google.com/macros/s/AKfycbx9bzA4y3tpmLmnjh0PV5XqvnvBHzPFj4c1LYc5y-tYRBdLaVbp5f7qMjr2C-KQZd3d/exec?type=routine"
        )
        .then((res) => {
          let routine = res.data.routines;

          this.classes = routine.map((r) => {
            if (r.subject) {
              let s = r.subject;
              this.subjects.add(s);
            }
            return {
              ...r,
              date: new Date(r.date_time).getTime(),
            };
          });
          this.selectSubject("");
          if (this.upcoming.length < 3) {
            let upcoming = [];
            routine.forEach((r) => {
              if (new Date(r.date_time).getTime() >= Date.now()) {
                upcoming.push({
                  ...r,
                  date: new Date(r.date_time).getTime(),
                });
              }
            });

            this.upcoming = upcoming;
          }
        });
    },

    selectSubject(s) {
      this.se = s;
      this.selected = this.classes.filter((r) => {
        if (r.subject) {
          if (r.subject.toLowerCase().includes(s.toLowerCase())) {
            return r;
          }
        }
      });
    },
  },
  created() {
    this.getRoutineInfo();
  },
};
</script>
